const rqb = { required: true, trigger: 'blur' }
const rqc = { required: true, trigger: 'change' }
const rqbc = { required: true, trigger: ['blur', 'change'] }

// 
const pw = { pattern: /^[a-zA-Z0-9._]+$/, message: '.', trigger: ['blur', 'change'] }

// 
const username = {
  pattern: /^(?=.*[a-zA-Z])[a-zA-Z\d]+$/,
  message: '',
  trigger: ['blur', 'change']
}

// 
const account = [
  { ...rqb, message: '' },
  { min: 6, max: 16, message: '6 - 16', trigger: 'blur' },
  username
]

// 
const consignee = [
  { ...rqb, message: '' },
  { min: 2, max: 15, message: '2 - 15', trigger: 'blur' },
  {
    pattern: /^[a-zA-Z|\u4E00-\u9FA5| \u3400-\u4DB5]+$/,
    message: '2-15',
    trigger: ['blur', 'change']
  }
]

// 
const password = [
  { ...rqb, message: '' },
  { min: 8, max: 20, message: '8 - 20', trigger: 'blur' },
  pw
]

// 
const chineseCharacter = { pattern: /^[\u4E00-\u9FA5]+$/, message: '', trigger: ['blur', 'change'] }

// 
const number = { pattern: /^[0-9]+$/, message: '', trigger: ['blur', 'change'] }

// 
function isTel(v) {
  let isPhone = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/ // 
  let isMob = /^((0\d{2,3})-?)(\d{7,8})(-(\d{3,}))?$/ // 
  return isPhone.test(v) || isMob.test(v)
}

export { rqb, rqc, rqbc, username, consignee, password, chineseCharacter, number, isTel, pw, account }
