import { BasicLayout, UserLayout } from '@/layouts'

/* Router Modules */
import home from './modules/home'
import approve from './modules/approve'
import absent from './modules/absent'
import illLeave from './modules/illLeave'

// 
import template from './modules/template'

/**
 * hidden: false                  // sidebar401login/edit/1
 * alwaysShow: false              //  *
 * name:'RouterName'              //, , name
 * hideChildrenInMenu: -          //ItemSubItem( meta.hidden)
 * meta : {
    title: 'title'                //, , *
    icon: 'icon-class-name'       // menu 
    keepAlive: false              // ( multi-tab  true)
    permission: [100, 200]        // []
  }
 **/

const asyncRoutes = [home, approve, absent, illLeave]

// 
process.env.NODE_ENV === 'development' && asyncRoutes.push(template)

/**
 * 
 */
export const asyncRouterMap = [
  {
    path: '/',
    component: BasicLayout,
    meta: { title: '' },
    redirect: '/home',
    children: asyncRoutes
  },
  {
    path: '*',
    redirect: '/redirect'
  }
]

/**
 * 
 */
export const constantRouterMap = [
  {
    path: '/user',
    component: UserLayout,
    redirect: '/user/login',
    children: [
      {
        path: 'login',
        name: 'login',
        component: () => import(/* webpackChunkName: "user" */ '@/views/user/login')
      }
    ]
  },
  {
    path: '/redirect',
    component: () => import(/* webpackChunkName: "redirect" */ '@/views/redirect')
  }
]
