<template>
  <a-modal
    :custom-class="loading ? 'bg-grey' : ''"
    :mask-closable="false"
    :closable="!loading"
    v-bind="$attrs"
    @close="submitAction"
  >
    <template v-if="!loading">
      <p class="success-tip">
        
        <span class="big">{{ row.isDivideClass ? '' : '' }}</span>
        <span class="num">{{ row.studentSize }}</span>
        
        <span class="num">{{ row.insertCount }}</span>
        
        <span class="num">{{ row.updateCount }}</span>
        
      </p>
      <p v-show="tableData.length" class="success-tip">
        <span class="num">{{ tableData.length }}</span>
        {{ row.isDivideClass ? '' : '' }}
        <span v-show="row.stuUnCoverSize">
          
          <i class="num" style="font-style: normal;">{{ row.stuUnCoverSize }}</i>
          
        </span>
      </p>
      <template v-if="row.repeatIdcardSet.length">
        <p class="err-tip">
          
          <span class="num"></span>
          
          <span class="num"></span>
        </p>
        <div style="max-height: 200px; line-height: 1.5;">
          <p v-for="item in row.repeatIdcardSet" :key="item">{{ item }}</p>
        </div>
      </template>
      <template v-if="row.repeatNameSexBirthSet.length">
        <p class="err-tip">
          
          <span class="num">++</span>
          
          <span class="num"></span>
        </p>
        <div style="max-height: 200px; line-height: 1.5;">
          <p v-for="item in row.repeatNameSexBirthSet" :key="item">{{ item }}</p>
        </div>
      </template>

      <template v-if="tableData.length">
        <a-button type="primary" @click="row.errorExcelDownloadUrl && downExcel(row.errorExcelDownloadUrl)">
          
        </a-button>
        <p class="err-tip"></p>
        <el-table :data="tableData" border style="width: 100%;">
          <el-table-column prop="rowNum" label="" width="180">
            <template #default="scope">
              <p class="row-tip">
                
                <span class="num">{{ scope.row.rowNum }}</span>
                
              </p>
            </template>
          </el-table-column>
          <el-table-column prop="errMsg" label="" />
        </el-table>
      </template>
      <span slot="footer" class="dialog-footer">
        <!--<a-button @click="$emit('close')"> </a-button>-->
        <a-button type="primary" @click="submitAction"> </a-button>
      </span>
    </template>
    <div v-else class="loading-tip">
      <p><img src="@/assets/images/upload.gif" alt="" /></p>
      <p>....</p>
    </div>
    <!---->
    <sync-tip-model v-if="syncOpts.visible" append-to-body v-bind="syncOpts" @close="syncOpts.visible = false" />
  </a-modal>
</template>

<script>
export default {
  name: 'UploadResultModel',
  props: {
    row: {
      // 
      type: Object,
      default: () => {
        return {
          studentSize: 0,
          stuMSize: 0,
          stuFSize: 0,
          stuFailInfoList: '',
          repeatIdcardSet: [],
          repeatNameSexBirthSet: [],
          stuUnCoverSize: 0,
          taskSimpleVOS: '',
          insertCount: 0,
          updateCount: 0,
          isDivideClass: false // 
        }
      }
    }
  },
  data() {
    return {
      loading: true,
      tableData: [],
      syncOpts: {
        // 
        visible: false,
        title: '',
        width: '620px',
        row: {}
      }
    }
  },
  watch: {
    row(newVal) {
      const { stuFailInfoList, taskSimpleVOS: taskList, studentIdList: stuIds } = newVal
      stuFailInfoList && this.handleData(stuFailInfoList)
      taskList &&
        taskList.length &&
        setTimeout(() => {
          this.showSyncModel({ taskList, stuIds })
        }, 300)
    }
  },
  methods: {
    downExcel(url) {
      window.open(url, '_blank')
    },
    // 
    handleData(arr) {
      this.loading = true
      let dataArr = []
      if (arr.length > 0) {
        arr.forEach(item => {
          let errMsg = ''
          let newArr = []
          item.forEach(subItem => {
            newArr = subItem.split(',')
            if (newArr[1]) {
              let errBack = this.$g.listErrMsg[newArr[1]]
              errBack = errBack === '9999' ? newArr[2] || '' : errBack
              errMsg += errBack + ''
            }
          })
          dataArr.push({
            rowNum: newArr[0],
            errMsg: errMsg ? errMsg.substr(0, errMsg.length - 1) : ''
          })
        })
      }
      this.tableData = dataArr
      this.loading = false
    },
    submitAction() {
      this.$emit('close')
      this.$emit('close-parent')
    },
    // 
    showSyncModel(params) {
      this.syncOpts.visible = true
      this.syncOpts.row = {
        ...params,
        operatorType: 20 // 
      }
    }
  }
}
</script>

<style lang="less" scoped>
/deep/.bg-grey {
  .el-dialog__header {
    box-shadow: none;
  }
  .el-dialog__body {
    background: #f5f5f5;
  }
}
.success-tip {
  font-size: 16px;
  font-weight: bold;
  line-height: 25px;
  padding-bottom: 5px;
  .big {
    font-size: 20px;
  }
}
.err-tip {
  margin: 20px 0 10px;
  .num {
    font-size: 14px;
  }
}
.num {
  color: @red;
  font-size: 16px;
  font-weight: bold;
}
.loading-tip {
  font-size: 16px;
  color: #777;
  text-align: center;
  padding-bottom: 30px;
  img {
    width: 155px;
    margin: 30px 0;
  }
}
</style>
