import storage from '@/core/store'
import { router, resetRouter } from '@/router'
import { login, getUserInfo, logout, getPermission } from '@/api/system'
import { ACCESS_TOKEN } from '@/store/mutation-types'
import config from '@/config/settings'

const user = {
  namespaced: true,
  state: {
    token: '',
    name: '',
    userId: '',
    welcome: '',
    avatar: '',
    dataRangeType: '', //  0 1 2 3 4
    roles: {
      // 
      roleType: '', // 10-20-30-40-45-
      roleTypeName: '' // 
    },
    orgInfo: {
      // 
      orgId: '', // id
      orgType: '', //  10- 20- 30- 40-
      orgName: '', // 
      orgProperty: '', //   10- 20-  30- 40- 45-46- 50- 55-
      areaIds: [], //  //[provinceId,'','']  [provinceId,cityId,''] [provinceId,cityId,regionId] dataRangeType
      BelongArea: [] // 
    },
    permissions: [], // id
    microApps: [] // 
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
      storage.set(ACCESS_TOKEN, token, config.tokenExpires)
    },
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_USERID: (state, userId) => {
      state.userId = userId
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    },
    SET_PERMISSIONS: (state, permissions) => {
      state.permissions = permissions
    },
    SET_RANGE_TYPE: (state, type) => {
      state.dataRangeType = type
    },
    SET_ORGINFO: (state, orgInfo) => {
      state.orgInfo = orgInfo
    }
  },

  actions: {
    // 
    Login({ commit }, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo)
          .then(response => {
            const token = response.data.token
            commit('SET_TOKEN', token)
            resolve()
          })
          .catch(error => {
            reject(error)
          })
      })
    },

    // 
    GetInfo({ commit, dispatch, state }) {
      return new Promise((resolve, reject) => {
        getUserInfo()
          .then(async response => {
            const result = response.data

            !result?.userId && reject('')
            console.log(':', result)

            // let { docName, userId, avatar, roles } = result
            const {
              roleType,
              roleTypeName,
              realName,
              userId,
              orgId,
              orgType,
              orgName,
              orgProperty,
              provinceId,
              cityId,
              regionId,
              dataRangeType,
              dataRangeProvinceId,
              dataRangeCityId,
              dataRangeRegionId
            } = result
            // id,
            const areaIds =
              dataRangeType == 4
                ? [provinceId, cityId, regionId]
                : [dataRangeProvinceId, dataRangeCityId, dataRangeRegionId]

            commit('SET_NAME', realName)
            commit('SET_USERID', userId)
            // commit('SET_AVATAR', avatar)
            commit('SET_ROLES', { roleType, roleTypeName })
            commit('SET_RANGE_TYPE', dataRangeType)
            commit('SET_ORGINFO', {
              orgId,
              orgType,
              orgName,
              orgProperty,
              areaIds,
              BelongArea: [provinceId, cityId, regionId]
            })

            try {
              await dispatch('GetPermissions')
            } catch {
              reject('')
            }

            resolve(result)
          })
          .catch(error => {
            reject(error)
          })
      })
    },

    // 
    GetPermissions({ commit }) {
      return new Promise((resolve, reject) => {
        getPermission()
          .then(({ data }) => {
            commit('SET_PERMISSIONS', data)
            resolve()
          })
          .catch(error => {
            reject(error)
          })
      })
    },

    // 
    Logout({ commit, state: { token } }) {
      return new Promise(resolve => {
        logout(token)
          .then(() => {
            resolve()
          })
          .catch(() => {
            resolve()
          })
          .finally(() => {
            commit('SET_TOKEN', '')
            commit('SET_PERMISSIONS', [])
            commit('SET_ROLES', {})
            commit('SET_NAME', '')
            commit('SET_USERID', '')
            commit('SET_ORGINFO', {})

            commit('SET_ROUTERS', [], { root: true })
            storage.remove(ACCESS_TOKEN)
          })
      })
    },

    // 
    async UpdatePermissions({ dispatch, rootState: { permission } }) {
      try {
        // 
        await dispatch('GetPermissions')

        // 
        resetRouter()

        // 
        await dispatch('GenerateRoutes', {}, { root: true })

        // 
        router.addRoutes(permission.addRouters)
      } catch {
        // 
        dispatch('Logout').then(() => {
          window.location.reload()
        })
      }
    }
  }
}

export default user
